// frontend/src/pages/MainPage/MainPage.js
import React, { useState, useEffect, useRef, useCallback, useContext } from 'react';
import VoiceRecorder from '../../components/VoiceRecorder/VoiceRecorder';
import StatusBar from '../../components/StatusBar/StatusBar';
import apiClient from '../../services/apiClient'; 
import useTTS from '../../hooks/useTTS'; 
import useVoice from '../../hooks/useVoice';
import ConfirmationModal from '../../components/ConfirmationModal';
import ResultDisplay from '../../components/ResultDisplay';
import { motion, AnimatePresence } from 'framer-motion';
import { AuthContext } from '../../contexts/AuthContext';
import { useInteraction, INTERACTION_STATES } from '../../contexts/InteractionContext';
import { useVoiceCoordinator } from '../../hooks/useVoiceCoordinator';
import { createErrorHandler } from '../../utils/errorHandler';
import './MainPage.css'; 
import useIntent from '../../hooks/useIntent';
import { 
    MAIN_PAGE_STYLES, 
    getStateStyle, 
    shouldShowResetButton, 
    getContainerClassName, 
    getAnimationProps, 
    getComponentProps 
} from './MainPageStyles';

console.log('Test persistence');

const MainPage = () => {
    const { isAuthenticated, loading: authLoading } = useContext(AuthContext);
    
    // 
    const {
        currentState,
        lastTranscript,
        lastResponse,
        pendingAction,
        resultData,
        isConfirmModalOpen,
        confirmText,
        error,
        sessionId,
        setState,
        setTranscript,
        setResponse,
        setPendingAction,
        setResultData,
        setError,
        reset,
        closeConfirmModal,
        openConfirmModal
    } = useInteraction();
    
    // hooks
    const { speak, cancel: cancelTTS, isSpeaking } = useTTS(); 
    const { startListening, transcript: voiceTranscript, isListening, error: voiceError, reset: resetVoice, stopListening } = useVoice();
    const { classifyIntent } = useIntent();
    const voiceCoordinator = useVoiceCoordinator();
    
    // 
    const errorHandlerRef = useRef(null);
    if (!errorHandlerRef.current) {
        errorHandlerRef.current = createErrorHandler({ setError });
    }
    
    // 
    const resetTimerRef = useRef(null);

    useEffect(() => {
        if (!sessionId) {
            // sessionIdInteractionContext
            console.log('Session ID from context:', sessionId);
        }
        
        // MVP
    }, [isAuthenticated, authLoading, sessionId]);
    
    // 
    const resetUIState = useCallback(() => {
        console.log('...');
        setState(INTERACTION_STATES.IDLE);
        setPendingAction(null);
        closeConfirmModal();
        setResultData(null);
        voiceCoordinator.forceStopAll();
        // 
        // setTranscript('');
        // setResponse(null);
    }, [setState, setPendingAction, closeConfirmModal, setResultData, voiceCoordinator]);

    const handleVoiceResult = useCallback(async (transcript) => {
        // 
        if (!isAuthenticated) {
            console.error('');
            setState(INTERACTION_STATES.ERROR);
            setResponse({ status: 'error', message: '' });
            voiceCoordinator.speak('', resetUIState);
            return;
        }
        
        // 
        if (resetTimerRef.current) {
            clearTimeout(resetTimerRef.current);
            resetTimerRef.current = null;
        }
        
        if (!sessionId) {
            console.error('Session ID is not initialized.');
            setState(INTERACTION_STATES.ERROR);
            setResponse({ status: 'error', message: '' });
            return;
        }
        
        console.log(`[Session: ${sessionId}] Received transcript:`, transcript);
        setTranscript(transcript);
        setState(INTERACTION_STATES.THINKING);
        setResponse(null);
        setResultData(null);
        setPendingAction(null);
        closeConfirmModal();

        try {
            const result = await apiClient.interpret(transcript, sessionId, 1); 
            console.log(`[Session: ${sessionId}] Interpret API Result:`, result);

            // sessionIdInteractionContext

            if (result.tool_calls && result.tool_calls.length > 0) {
                console.log(`[Session: ${sessionId}] Tool call required. Pending action set.`);
                setPendingAction(result);
                const textToConfirm = result.confirm_text || result.confirmText || '';
                openConfirmModal(textToConfirm);

                //  speak  onEnd 
                voiceCoordinator.speak(textToConfirm, () => {
                    // TTS  STT 
                    console.log("MainPage: ...");
                    console.log("[MainPage] speak onEnd callback triggered. Current status before set:", currentState);
                    setState(INTERACTION_STATES.CONFIRMING);
                    
                    // ConfirmationModalprops
                    console.log("[MainPage] CONFIRMINGConfirmationModal");
                });
            } else if (result.action === 'respond' && result.content) {
                console.log(`[Session: ${sessionId}] Direct response received.`);
                setResponse({ status: 'info', message: result.content });
                setState(INTERACTION_STATES.SPEAKING);
                voiceCoordinator.speak(result.content, resetUIState);
            } else {
                const textToConfirm = result.confirm_text || result.confirmText;
                if (textToConfirm) {
                    console.log(`[Session: ${sessionId}] Confirmation text only received.`);
                    setPendingAction(result);
                    openConfirmModal(textToConfirm);
                    voiceCoordinator.speak(textToConfirm, () => {
                        console.log("MainPage: ()...");
                        console.log("[MainPage] speak onEnd callback triggered (no tool call). Current status before set:", currentState);
                        setState(INTERACTION_STATES.CONFIRMING);
                        
                        // ConfirmationModalprops
                        console.log("[MainPage] CONFIRMINGConfirmationModal");
                    });
                } else {
                    console.log(`[Session: ${sessionId}] Response format detection:`, result);
                    const message = JSON.stringify(result);
                    setResponse({ status: 'info', message: `: ${message}` });
                    setState(INTERACTION_STATES.SPEAKING);
                    voiceCoordinator.speak("", resetUIState);
                }
            }
        } catch (error) {
            console.error(`[Session: ${sessionId}] Interpret API call failed:`, error);
            const message = `${error.message || ''}`;
            setResponse({ status: 'error', message });
            setState(INTERACTION_STATES.ERROR);
            voiceCoordinator.speak(message, resetUIState);
            
            // 5
            resetTimerRef.current = setTimeout(resetUIState, 5000);
        }
    }, [voiceCoordinator, resetUIState, isAuthenticated, sessionId, setState, setTranscript, setResponse, setResultData, setPendingAction, closeConfirmModal, openConfirmModal, currentState]);

    // 
    const executeToolAndHandleResult = useCallback(async (toolId, params, currentSessionId, userId) => {
        // 
        if (!isAuthenticated) {
            console.error('');
            setState('ERROR');
            setResultData({ status: 'error', message: '' });
            voiceCoordinator.speak('');
            return;
        }
        
        try {
            setState('THINKING');
            const execResult = await apiClient.execute(toolId, params, currentSessionId, userId);
            
            if (execResult.success && execResult.data) {
                setResultData({ status: 'success', data: execResult.data });
                
                // 
                const textToSpeak = execResult.data?.summary || 
                                  execResult.data?.tts_message || 
                                  execResult.data?.message || 
                                  execResult.data?.result || 
                                  '';
                
                voiceCoordinator.speak(textToSpeak);
                setState('IDLE');
            } else {
                throw new Error(execResult.error?.message || '');
            }
        } catch (error) {
            errorHandlerRef.current.handleError(error, 'TOOL_EXECUTION');
        }
    }, [isAuthenticated, setState, setResultData, voiceCoordinator]);

    const handleUserConfirm = useCallback(() => {
        console.log(`[Session: ${sessionId}] `);
        closeConfirmModal();
        
        // pendingAction
        let toolId, params, userId = 1;
        
        if (pendingAction?.tool_calls && pendingAction.tool_calls.length > 0) {
            const firstToolCall = pendingAction.tool_calls[0];
            toolId = firstToolCall.tool_id;
            params = firstToolCall.parameters || {};
        } else {
            toolId = pendingAction?.action || 'default_confirm_action';
            params = pendingAction?.params || {};
        }
        
        console.log(`[Session: ${sessionId}] : ${toolId}:`, params);
        
        // 
        executeToolAndHandleResult(toolId, params, sessionId, userId);
    }, [pendingAction, closeConfirmModal, sessionId, executeToolAndHandleResult]);

    const handleUserRetry = useCallback(() => {
        closeConfirmModal();
        setPendingAction(null);
        setState(INTERACTION_STATES.IDLE);
        console.log('User chose to retry.');
        // 
        setTimeout(() => {
            voiceCoordinator.startListening();
        }, 500);
    }, [closeConfirmModal, setPendingAction, setState, voiceCoordinator]);

    const handleUserCancel = useCallback(() => {
        console.log(`[Session: ${sessionId}] `);
        closeConfirmModal();
        setPendingAction(null);
        setState(INTERACTION_STATES.IDLE);
        voiceCoordinator.speak("");
    }, [closeConfirmModal, setPendingAction, setState, sessionId, voiceCoordinator]);

    useEffect(() => {
        if (!voiceCoordinator.isTTSActive && (currentState === INTERACTION_STATES.SPEAKING || currentState === INTERACTION_STATES.ERROR)) {
            console.log('TTS finished, setting state to idle.');
            setState(INTERACTION_STATES.IDLE);
        }
    }, [voiceCoordinator.isTTSActive, currentState, setState]);

    useEffect(() => {
        return () => {
            // 
            voiceCoordinator.forceStopAll();
            if (resetTimerRef.current) {
                clearTimeout(resetTimerRef.current);
            }
        };
    }, [voiceCoordinator]);

    const handleVoiceError = useCallback((error) => {
        console.error('VoiceRecorder Error:', error);
        
        // 
        const knownHarmlessErrors = [
            'no-speech',
            'audio-capture', 
            'not-allowed'
        ];
        
        if (knownHarmlessErrors.includes(error.error)) {
            console.log(':', error.error);
            return;
        }
        
        errorHandlerRef.current.handleError(error, 'VOICE_RECOGNITION');
    }, []);
    
    // 
    const handleReset = useCallback(() => {
        console.log('Manual reset triggered');
        
        // 
        voiceCoordinator.forceStopAll();
        
        // 
        reset();
        
        // 
        if (resetTimerRef.current) {
            clearTimeout(resetTimerRef.current);
            resetTimerRef.current = null;
        }
    }, [voiceCoordinator, reset, resetVoice]);
    
    // 

    // 
    const handleVoiceConfirmation = useCallback((voiceInput) => {
        console.log("[MainPage] :", voiceInput);
        if (!voiceInput || currentState !== INTERACTION_STATES.CONFIRMING) return;
        
        // 
        const input = voiceInput.toLowerCase();
        let intent = 'UNKNOWN';
        
        if (input.includes('') || input.includes('') || input.includes('') || input.includes('')) {
            intent = 'CONFIRM';
        } else if (input.includes('') || input.includes('') || input.includes('')) {
            intent = 'CANCEL';
        } else if (input.includes('') || input.includes('') || input.includes('')) {
            intent = 'RETRY';
        }
        
        console.log("[MainPage] :", intent);
        
        // 
        switch (intent) {
            case 'CONFIRM':
                console.log("[MainPage] ");
                handleUserConfirm();
                break;
            case 'RETRY':
                console.log("[MainPage] ");
                handleUserRetry();
                break;
            case 'CANCEL':
                console.log("[MainPage] ");
                handleUserCancel();
                break;
            default:
                console.log("[MainPage] ");
                // 
                voiceCoordinator.speak("");
                break;
        }
    }, [currentState, handleUserConfirm, handleUserRetry, handleUserCancel, voiceCoordinator]);
    
    // useEffect
    useEffect(() => {
        if (currentState === INTERACTION_STATES.CONFIRMING && lastTranscript && !voiceCoordinator.isSTTActive) {
            // 
            console.log("[MainPage] : /:", lastTranscript);
            handleVoiceConfirmation(lastTranscript);
        }
    }, [currentState, lastTranscript, voiceCoordinator.isSTTActive, handleVoiceConfirmation]);

    // 
    useEffect(() => {
        if (voiceCoordinator.errorMessage) {
            console.error(":", voiceCoordinator.errorMessage);
            
            // 
            if (currentState === INTERACTION_STATES.CONFIRMING) {
                 voiceCoordinator.speak("", () => {
                    setState(INTERACTION_STATES.CONFIRMING);
                    // TTS
                    setTimeout(() => {
                        voiceCoordinator.startListening();
                    }, 500);
                 });
            } else {
                 // 
                 reset();
            }
        }
    }, [voiceCoordinator.errorMessage, currentState, voiceCoordinator, reset, setState]);

    // UI Rendering
    return (
        <motion.div
            className={getContainerClassName(currentState)}
            data-testid="main-page"
            {...getAnimationProps('pageTransition')}
        >
            <StatusBar 
                {...getComponentProps('statusBar')}
                currentStatus={currentState} 
                lastTranscript={lastTranscript} 
                lastResponse={lastResponse} 
            />
            
            <div {...getComponentProps('contentWrapper')}>
                {/*  */}
                <div {...getComponentProps('contentArea')}>
                    <div {...getComponentProps('messagesContainer')}>
                        {lastTranscript && (
                            <motion.div 
                                className="transcript user-message"
              initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.3 }}
            >
                                <div className="message-header"></div>
                                <div className="message-content">{lastTranscript}</div>
                            </motion.div>
                        )}
                        
                        {typeof lastResponse === 'string' && lastResponse && (
                            <motion.div 
                                className="ai-response system-message"
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.3 }}
                            >
                                <div className="message-header">AI</div>
                                <div className="message-content">{lastResponse}</div>
                            </motion.div>
                        )}
                        
                        {lastResponse && typeof lastResponse === 'object' && lastResponse.status !== 'success' && (
                            <motion.div
              initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.3 }}
                                className="result-container"
            >
              <ResultDisplay
                                    status={lastResponse.status}
                                    message={lastResponse.message} 
                                    autoSpeak={false}
              />
                            </motion.div>
                        )}
                        
                        {resultData && (
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.3 }}
                                className="result-container"
                            >
                                <ResultDisplay 
                                    status={resultData.status}
                                    data={resultData.data} 
                                    message={resultData.message}
                                    autoSpeak={true}
                                    onDismiss={handleReset}
                                />
                            </motion.div>
                        )}
                    </div>
        
        {/*  */}
                    {shouldShowResetButton(currentState) && (
                        <motion.button 
                            {...getComponentProps('resetButton')}
                            onClick={handleReset}
                            {...getAnimationProps('resetButton')}
                        >
                            
                        </motion.button>
                    )}
                    
                    {/*  */}
                    <VoiceRecorder 
                        {...getComponentProps('voiceRecorder')}
                        onResult={handleVoiceResult} 
                        onError={handleVoiceError}
                        setStatus={setState}
                        disabled={currentState !== INTERACTION_STATES.IDLE && currentState !== INTERACTION_STATES.LISTENING} // idlelistening
                    />
      </div>
            </div>
            
            {/*  */}
            <AnimatePresence>
                {isConfirmModalOpen && (
                    <ConfirmationModal
                        {...getComponentProps('confirmationModal')}
                        isOpen={isConfirmModalOpen}
                        confirmText={confirmText}
                        onConfirm={handleUserConfirm}
                        onRetry={handleUserRetry}
                        onCancel={handleUserCancel}
                        isListening={voiceCoordinator.isSTTActive}
                        isTTSSpeaking={voiceCoordinator.isTTSActive}
                        useVoiceConfirmation={true}
                        startSTTListening={voiceCoordinator.startListening}
                        stopSTTListening={voiceCoordinator.stopListening}
                    />
                )}
            </AnimatePresence>
        </motion.div>
    );
};

export default MainPage;